.register-user-container {
  
  margin: 60px auto;
  padding: 50px 30px;
   background: linear-gradient(to right, #f2fbff, rgb(92, 191, 244)); 
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  font-family: 'Segoe UI', sans-serif;
  max-width: 1200px;
}

.register-user-container h2 {
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 25px;
  color: #333;
}

.register-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-size: 14px;
  font-weight: 600;
  color: #555;
  margin-bottom: 6px;
}

.form-group input {
  padding: 12px 14px;
  font-size: 15px;
  border: 1px solid #ccc;
  border-radius: 10px;
  transition: border-color 0.3s, box-shadow 0.3s;
}

/* ðŸŒŸ Placeholder styling */
.form-group input::placeholder {
  color: #999;
  font-style: italic;
  font-size: 14px;
  opacity: 1;
}

.form-group input:focus {
  outline: none;
  border-color: #4a90e2;
  box-shadow: 0 0 0 3px rgba(74, 144, 226, 0.2);
}

button[type="submit"] {
  padding: 14px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}
