.invoice-container {
  max-width: 800px;
  margin: auto;
  background: #fff;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 0 12px rgba(0,0,0,0.1);
  font-family: Arial, sans-serif;
}

.invoice-container h2 {
  margin-bottom: 10px;
  color: #1e293b;
}

.invoice-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.invoice-table th,
.invoice-table td {
  padding: 10px;
  border-bottom: 1px solid #ccc;
  text-align: left;
}

.totals {
  margin-top: 20px;
  font-size: 16px;
  color: #111;
}

.loading-text {
  text-align: center;
  margin-top: 40px;
  font-size: 18px;
}
