/* Layout.css */

.layout-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
   background: linear-gradient(to right, #f2fbff, rgb(4, 160, 244)); 
  color: black;
  padding: 10px 20px;
  flex-wrap: wrap;
  position: sticky;
  top: 0;
  z-index: 1000;
  border-radius: 8px;
}

/* Brand Title */
.brand {
  font-size: 20px;
  font-weight: bold;
  margin-right: 30px; /* âœ… Space between brand and first link */
}

/* Hamburger Menu Icon (Mobile) */
.menu-toggle {
  display: none;
  font-size: 24px;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

/* Navigation Links */
.nav-links {
  display: flex;
  align-items: center;
  gap: 20px;
}

.nav-links a {
  color: #02293a ;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s;
}

.nav-links a:hover {
  color:rgb(4, 87, 243) ;
}

/* Content Padding */
.layout-content {
  padding: 20px;
}

/* âœ… Responsive Styles */
@media (max-width: 768px) {
  .menu-toggle {
    display: block;
    margin-left: auto;
  }

  .nav-links {
    flex-direction: column;
    width: 100%;
    display: none;
    padding-top: 10px;
  }

  .nav-links.active {
    display: flex;
  }

  .nav-links a {
    padding: 10px 0;
    border-top: 1px solid #334155;
  }
}
