.invoice-search-container {
  padding: 20px;
}

h2 {
  margin-bottom: 20px;
}

.filters {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 20px;
}

.filters input, .filters button {
  padding: 8px;
  font-size: 14px;
}

.filters button {
  background-color: #1e40af;
  color: white;
  border: none;
  cursor: pointer;
}

.filters button:hover {
  background-color: #1e3a8a;
}

.invoice-table {
  width: 100%;
  border-collapse: collapse;
}

.invoice-table th, .invoice-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.invoice-table th {
  background-color: #f1f5f9;
}
