.edit-container {
  max-width: 400px;
  margin: 50px auto;
  padding: 25px 30px;
  background: #f9f9f9;
  border-radius: 10px;
  font-family: Arial, sans-serif;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.edit-container h2 {
  text-align: center;
  margin-bottom: 25px;
  font-size: 22px;
  color: #333;
}

.edit-form label {
  display: block;
  margin-bottom: 15px;
  font-weight: 500;
  color: #444;
}

.edit-form input {
  width: 100%;
  padding: 10px 12px;
  margin-top: 5px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 15px;
  box-sizing: border-box;
  background-color: #fff;
}

/* Plus/Minus Button Group */
.input-group {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 5px;
}

.input-group input {
  flex: 1;
  text-align: center;
  padding: 10px 12px;
  font-size: 15px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background: #fff;
}

.input-group button {
  padding: 8px 14px;
  font-size: 18px;
  background-color: #e0e0e0;
  color: #333;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.input-group button:hover {
  background-color: #d5d5d5;
}

.update-btn {
  width: 100%;
  padding: 12px;
  background-color: #0c46c4;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.update-btn:hover {
  background-color: #13379b;
}

.back-button {
  margin-top: 12px;
  width: 100%;
  padding: 10px;
  background-color: #e0e0e0;
  color: #333;
  border: none;
  border-radius: 5px;
  font-size: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.back-button:hover {
  background-color: #d5d5d5;
}

.loading,
.error {
  text-align: center;
  font-size: 16px;
  color: #d9534f;
  margin-top: 20px;
}

@media (max-width: 900px) {
  .edit-container {
    padding: 10px;
    max-width: 100vw;
  }
}

@media (max-width: 600px) {
  .edit-container, h2, h1 {
    font-size: 18px !important;
  }
  .edit-form {
    gap: 8px !important;
  }
  input, select, button {
    font-size: 15px !important;
    width: 100% !important;
    box-sizing: border-box !important;
  }
}

@media (max-width: 400px) {
  .edit-container {
    padding: 2vw !important;
  }
  .edit-form {
    gap: 4px !important;
  }
}

/* Stylish GST Rate select and label */
.gst-label {
  display: flex;
  flex-direction: column;
  margin-bottom: 18px;
  font-weight: 500;
}
.gst-heading {
  font-size: 1.08em;
  margin-bottom: 6px;
  color: #2d7a2d;
  font-weight: bold;
  letter-spacing: 0.5px;
}
.gst-select {
  padding: 8px 12px;
  border-radius: 6px;
  border: 1.5px solid #2d7a2d;
  font-size: 1em;
  background: #f6fff6;
  margin-bottom: 4px;
  transition: border 0.2s;
}
.gst-select:focus {
  border: 2px solid #1e4d1e;
  outline: none;
}
.gst-desc {
  color: #4b4b4b;
  font-size: 0.93em;
  margin-top: 2px;
  margin-left: 2px;
  font-style: italic;
}
